@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
.header .barra {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .header .barra {
    flex-direction: row;
  }
}
.header .barra a {
  position: relative;
}
.header .barra a h1 {
  color: #1F5889;
  font-size: 4rem;
  font-weight: 700;
}
.header .barra a p {
  font-size: 1rem;
  font-weight: 700;
  color: #1F5889;
  margin: 0;
  position: relative;
  top: -2rem;
}
.header .barra span {
  color: #2B9CA4;
}
.header .barra .logo {
  margin: 0;
}

.navegacion {
  display: flex;
  flex-direction: column;
  z-index: 10;
  align-items: center;
}
.navegacion a:not(:last-of-type) {
  color: #2B9CA4;
  font-size: 1.5rem;
  font-weight: 700;
}
.navegacion a:not(:last-of-type):hover {
  color: #1F5889;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
    min-width: 45rem;
  }
}

.no-webp .banner-portada {
  background-image: url("../img/banner3.png");
}

.webp .banner-portada {
  background-image: url("../img/banner3.webp");
}

.header {
  position: relative;
}
.header .banner-portada {
  height: 65rem;
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.header .banner-portada .texto-banner {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #FFFFFF;
  z-index: 10;
}
.header .banner-portada h2, .header .banner-portada p {
  margin: 0;
  color: #FFFFFF;
  text-align: left;
  line-height: 5rem;
}
.header .banner-portada h2 {
  font-size: 7rem;
  font-weight: 700;
}
.header .banner-portada .banner-blur {
  background-color: rgba(5, 5, 5, 0.3);
  position: absolute;
  top: 0;
  height: 65rem;
  width: 100%;
}

@media (min-width: 768px) {
  .contenido-servicios {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 2rem;
  }
}
.contenido-servicios h3 {
  text-transform: uppercase;
  margin-bottom: 1rem;
  height: 10rem;
}
.contenido-servicios img {
  height: 10rem;
  width: auto;
}
.contenido-servicios picture {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenido-servicios .servicio {
  text-align: center;
}

@media (min-width: 768px) {
  .contenido-proyectos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
  }
}
.contenido-proyectos .proyecto {
  background-color: #1F5889;
  margin-bottom: 2rem;
}
.contenido-proyectos .proyecto .texto-proyecto {
  padding: 1rem;
}
.contenido-proyectos .proyecto h3, .contenido-proyectos .proyecto p {
  color: #FFFFFF;
  margin: 1rem 2rem;
  text-align: center;
}
.contenido-proyectos .proyecto a {
  width: 90%;
  margin: 2rem auto;
}

.proyectos a {
  margin-top: 3rem;
}

.webp .imagen-contacto {
  background-image: url(/build/img/oficinas.webp);
}

.no-webp .imagen-contacto {
  background-image: url(/build/img/oficinas.png);
}

.imagen-contacto {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  height: 50rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
  z-index: 10;
}
.imagen-contacto p {
  margin-bottom: 2rem;
}
.imagen-contacto .imagen-blur {
  background-color: rgba(5, 5, 5, 0.497);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}

.contenido-nosotros {
  background-color: #1F5889;
}
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
  .contenido-nosotros picture {
    grid-column: 2/3;
  }
  .contenido-nosotros .texto-nosotros {
    grid-row: 1/2;
  }
}
.contenido-nosotros .texto-nosotros {
  padding: 2rem;
}
.contenido-nosotros .texto-nosotros p {
  color: #FFFFFF;
}

.footer {
  background-color: #FFFFFF;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer h3 {
  margin: 0;
  font-size: 2rem;
  text-align: start;
  color: grey;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
  display: flex;
  justify-content: space-between;
}
.footer .contenido-footer .navegacion {
  visibility: visible;
  opacity: 1;
  width: 50rem;
  height: auto;
}
.footer .footer__parrafo {
  text-align: center;
  margin-bottom: 0;
  font-size: 1.5rem;
  color: grey;
}

.contenido-contacto p {
  text-align: center;
  margin: 0;
  line-height: 1.5;
  color: grey;
}
.contenido-contacto p:last-of-type {
  margin-bottom: 2rem;
}

.formulario fieldset {
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
.formulario fieldset .input {
  display: flex;
  flex-direction: column;
  margin: 1rem 0;
}
.formulario fieldset .input label {
  width: 10rem;
}
.formulario fieldset .input input, .formulario fieldset .input textarea {
  border: 1px solid grey;
  border-radius: 0.3rem;
  padding: 0.5rem;
}
.formulario fieldset .input textarea {
  height: 15rem;
}